:root {
  --brand-1: #9b9b9b;
  --brand-2: #ffffff;
  --brand-3: #000000;
  --marked-color: #fff;
  --shadow-1: 0 3px 4px 0 rgba(0, 0, 0, 0.04);
  --shadow-2: 0 4px 8px 0 rgba(0, 0, 0, 0.08);
  --shadow-3: 0 6px 10px 0 rgba(0, 0, 0, 0.08);
}
.ChatApp,
.MessageContainer,
.Navbar,
.Message .Bubble,
.QuickReplies,
.ChatFooter {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.5rem;
}
.ChatApp {
  background-color: #ffffff;
  font-size: 1.5rem;
}
.Message.left .Bubble {
  background-color: #000000;
  color: #fff;
  width: auto;
}
.Message.right .Bubble {
  background-color: #000000;
  color: #fff;
  width: auto;
}
.Input{
  font-size: 1.5rem;
  caret-color: black;
}
.Navbar-title{
  font-size: 2rem;
  font-weight: bold;
  
}
.Composer-sendBtn{

  background-color: aqua;
  width: 100px;
}

.Avatar img{
  width: 75px;
  height: 75px;
}
.Btn--primary {
  color: #0e0101;
}
.Message.left .Bubble {
  border-radius: 4px 20px 20px 20px;
}
.Message.right .Bubble {
  border-radius: 20px 4px 20px 20px;
}
.Card {
  border-radius: 12px;
}
.Btn {
  border-radius: 1px;

}
.QuickReply {
  border-radius: 20px;
}
.ActionItems{
  font-size: xx-large;
  padding-bottom: 20px ;

}
.ActionItems:hover {
  font-size: xx-large;
  background-color: #a5a3a3;
  color: #ffffff;
  padding-bottom: 20px ;


}

:root { --font-family: Georgia, serif;; }
